.details {
  line-height: 140%;
  color: #fff;
  text-align: left;

  padding-right: 20px;

  &__wrapper {
    display: grid;
    row-gap: 40px;
    width: 100%;

    @include on-tablet {
      row-gap: 48px;
    }
  }

  &__detail {
    &-img {
      @include page-grid;

      &-a {
        position: relative;
        &--wide {
          @include on-tablet {
            grid-column: span 4;
          }

          @include on-desktop {
            grid-column: span 8;
          }
        }

        &--square {
          height: 0;
          padding-bottom: 100%;

          @include on-tablet {
            grid-column: span 2;
          }

          @include on-desktop {
            grid-column: span 4;
          }
        }
      }
      &--photo {
        position: absolute;
        height: 100%;
        width: 100%;
        object-fit: cover;

        @include hover(transform, scale(110%));

      }
    }

    &--title {
      padding-top: 16px;
      font-size: 24px;
      font-weight: 700;
    }

    &--text {
      padding-top: 16px;
      font-size: 16px;
      font-weight: 400;
    }
  }

  &__button {
    grid-column: 1/-1;

    @include on-tablet {
      width: 392px;
      margin-top: 48px;
      grid-column: 2/-2;
    }

    @include on-desktop {
      width: 324px;
      margin-top: 62.5px;
      grid-column: 5/9;
    }

    margin-top: 40px;
    margin-inline: auto;
    display: flex;
    height: 56px;
    width: 278px;
    background-color: #fff;
    justify-content: center;
    align-items: center;
    border-radius: 40px;

    &:hover {
      box-shadow: 1px 4px 16px 0 #e8e8f066;
    }

    &:focus {
      box-shadow: 1px 4px 16px 0 #e8e8f0a3;
    }

    &-explore {
      font-size: 16px;
      font-weight: 700;
      line-height: 27px;
      color: #000;
      text-decoration: none;
    }
  }
}
